export default function (router) { 
	router.map ({
		'/':{
			name: 'home',
			component:require('./App.vue')
		},
		'/list':{
			name: 'list',
			component:require('./views/list.vue')
		},
		'/async': {
			name: 'async',
		    component: function (resolve) {
		      require(['./views/haha.vue'], resolve)
		      require(['./views/kkk.vue'], resolve)
		    }
		  },
		  '/kkk': {
			name: 'kkk',
		    component:require('./views/kkk.vue')
		  },
		  '/cart': {
		  	bodyClass:'bot-padding',
			name: 'cart',
		    component:require('./views/cart.vue')
		  },
		  '/list': {
		  	bodyClass:'bot-padding',
			name: 'list',
		    component:require('./views/list.vue')
		  },
		  '/user': {
		  	bodyClass:'user-bg',
			name: 'user',
		    component:require('./views/user.vue'),
		    auth: true
		  },
		  '/login': {
		  	name: 'login',
		  	component:require('./views/login.vue')
		  }
	})

}



