<template>
	<header id="header">
		<div class="top-icon-left" v-if="backBar" @click="backpages(pageLink)" ></div>
		<h1 v-text="pageTitle">首页</h1>
	</header>
</template>
<script>
export default {
	props : ['pageTitle','pageLink','backBar'],
	data () {
		return {
		  msg: 'Hello World!'
		}
	},
	methods: {
		backpages (link){
			let self = this;
      
		      if(link === 'back'){
		        window.history.go(-1);
		      }else{
		        self.$route.router.go('/'+link);
		      }
		}
	}
}
</script>
<style lang="stylus">
@import '../assets/css/index'
#header
	background-color #f60
	text-align center
	height px2rem(100)
	position relative
	h1
		margin 0
		color white
		line-height px2rem(100)
		font-size px2rem(36)
	.top-icon-left
		position absolute
		width px2rem(100)
		height 100%
		&:before
			display: block;
			content: '';
			position:absolute;
			width: px2rem(20);
			height: px2rem(20);
			border-top:3px solid #fff;
			border-left:3px solid #fff;
			top:50%;
			left:50%;
			transform: translate(-50%,-50%) rotate(-45deg);

</style>


