<template>
	<nav-head page-title="商品中心" back-bar="true" page-link=""></nav-head>	
	<h1 class="haha">商品中心</h1>
	<button type="button" class="btn" @click="login">登录</button>
</template>
<script>

export default {
	data (){
		let self = this;
		return{
			alert: {
                txt: '',
                show: false,
                hideFn:function(times){
                    let timer = times ? times: 2000;
                    setTimeout(function () {
                        self.alert.show = false;
                    }, timer);
                }
            }
		}
	},
    components: {
    	navHead: require('../components/header.vue')
    },
    methods: {
  		login (){
  			let self = this;
            localStorage.loginStatus = true;
            let redirect = decodeURIComponent(self.$route.query.redirect || '/');
            self.alert.txt = '登录成功';
            self.alert.show = true;
            setTimeout(function(){
            	self.alert.hideFn()
				self.$route.router.go(redirect);
            },2000);
  		}
    }
}
</script>
<style lang="stylus">
@import '../assets/css/base'

</style>


